.container {
  padding-left: 16px;
  padding-right: 16px;
  @media (min-width: 768px) {
    padding-left: 50px;
    padding-right: 50px;
  }
  @media (min-width: 1024px) {
    padding-left: 112px;
    padding-right: 112px;
  }

}

.list {
  list-style: none;
  padding: 0;
  margin: 0;
  touch-action: none;
  margin-top: 40px;
  min-height: 400px;


}

.dropzone {
  min-height: 400px;


}


.item {
  cursor: grabbing;
  font-family: 'Roboto';
  min-height: 280px;
  background-color: #ffffff;
  opacity: 1 !important;

}

.gridItem:hover {

  transform: scale(1.05);
  box-shadow: 25px -17px 24px -21px rgba(0, 0, 0, 0.35);


}

.gridItem {
  width: calc(100% - 14px);
  height: 95%;
  border: 1px solid #e7e7e7;
  border-radius: 6px;
  padding: 20px;

}


.name {
  font-size: 20px;
  color: #000000;
  font-weight: 600;
  margin-bottom: 10px;
  padding-right: 10px;

  &:hover {
    text-decoration: underline;
  }
}

.owner {
  color: #000000;
  font-size: 14px;
  margin-bottom: 10px;
}

.stats {
  display: grid;
  grid-template-columns: max-content max-content max-content max-content;
  column-gap: 20px;
  justify-content: center;
  @media (min-width: 768px) {
    column-gap: 35px;
  }
  @media (min-width: 1024px) {
    column-gap: 20px;
  }
  @media (min-width: 1440px) {
    column-gap: 35px;
  }

  div {
    display: flex;
    align-items: center;
  }
}

.icon {
  width: 24px;
  height: 24px;
  margin-right: 7px;
}


.delete {
  cursor: pointer;
  position: absolute;
  top: 16px;
  right: 30px;

  svg:hover {
    transform: scale(2);
  }
}

.chart {
  margin-top: 30px;
  width: 100%;
  height: 60%;
  color: #3861e1;
  //padding-bottom: 20px;
}